	module.exports = {"data":{"wordpressPost":{"title":"Chosungah &#8211; The Woman Behind The Most Famous Korean Beauty Brand EVER","content":"<p>In recent times Korean beauty has captured our attention and it&#8217;s easy to see why. Korean women are gorgeous. Their skin is fresh and dewy and their makeup is flattering too.</p>\n<p>Given that, it&#8217;s not surprising we want to know their secrets. Boy do we want to! So when we heard that Chosunga, Korea&#8217;s MOST famed makeup artist and founder of the <strong>cult Korean brands Chosungah 22 and 16 Brand</strong> was in town we demanded that she speak to us! Not really &#8211; we just asked politely and she said yes. And this is what she told us.</p>\n<p>&nbsp;</p>\n<figure id=\"attachment_2838\" style=\"width: 300px\" class=\"wp-caption aligncenter\"><img class=\"size-medium wp-image-2838\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06230815/JPG-Chosungah-Mecca-300x104.jpg\" alt=\"\" width=\"300\" height=\"104\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06230815/JPG-Chosungah-Mecca-300x104.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06230815/JPG-Chosungah-Mecca.jpg 714w\" sizes=\"(max-width: 300px) 100vw, 300px\" /><figcaption class=\"wp-caption-text\">The artist at work &#8211; Chosungah.</figcaption></figure>\n<p>&nbsp;</p>\n<p><strong>K-Beauty has swept the world. Why do you think that is?</strong><br />\nK-Beauty has a signature look. It&#8217;s all about the glowy complexion and healthy looking skin. I think that is probably what caught people’s attention. It’s something that makes you look beautiful without the need for a lot of makeup. I also think it’s the result of K Pop culture becoming big around the world.</p>\n<p><strong>What does typical daily skincare look like for your average Korean woman?</strong><br />\nA lot of it comes from Korea’s traditional and very popular sauna culture. Today in Korea, clinic skincare appointments are very popular and form part of everyday life for many women. We bring that home too. We use products every day such as masks. The sheet mask originated in Korea. Korean women have one on every night, not just occasionally. Korean women know that the most important thing to do is to keep skin hydrated.</p>\n<img class=\"size-medium wp-image-2849 aligncenter\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06235356/JPG-Donkey-300x253.jpg\" alt=\"\" width=\"300\" height=\"253\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06235356/JPG-Donkey-300x253.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06235356/JPG-Donkey-768x648.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06235356/JPG-Donkey.jpg 904w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p><a href=\"http://www.mecca.com.au/16-brand/donkey-milk-cream/I-027691.html?cgpath=brands-16bran#start=1\">16 brand Donkey Milk Cream, $29.</a></p>\n<p><strong>What do you love most about K-beauty?</strong><br />\nThe passion. Korean women are very passionate about skincare and makeup. Chosungah Beauty (which includes Chosungah 22 and 16 Brand) revolves around ignoring blemishes and/or perceived flaws. We don&#8217;t want you to focus on anything negative or feel sad. We&#8217;re all about celebrating the good things, the beautiful features you have and highlighting those.</p>\n<p><strong>What did you learn about beauty as a girl and who taught you?</strong><br />\nAs a child I would spend a lot of time playing with makeup products and when my friends would come over, I would dress them up and do their makeup and we would play beauty pageants in my living room. It was very much a part of my everyday life!</p>\n<p><strong>Favourite international beauty muse?</strong><br />\nAudrey Hepburn but not so much in her prime but when she was older. The way she kept herself beautiful is something that definitely inspires me. To age with confidence, grace and that cool factor is something I’d like to achieve.</p>\n<img class=\"size-medium wp-image-2843 aligncenter\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06233720/JPG-Chos-palette-300x226.jpg\" alt=\"\" width=\"300\" height=\"226\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06233720/JPG-Chos-palette-300x226.jpg 300w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06233720/JPG-Chos-palette-768x579.jpg 768w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06233720/JPG-Chos-palette.jpg 904w\" sizes=\"(max-width: 300px) 100vw, 300px\" />\n<p>&nbsp;</p>\n<p><a href=\"http://www.mecca.com.au/chosungah22/dong-gong-minn-jello-colour-eyeshadow-palette/V-025700.html?cgpath=brands-chosun#start=1\">Chosungah22 Dong Gong Minn Jello Colour Eyeshadow Palette, $49.</a></p>\n<p><strong>Favourite Korean beauty muse?</strong><br />\nI don&#8217;t admire one particular person or celebrity but rather all Korean woman and their passion for beauty. The everyday woman is striving for better beauty which I love.</p>\n<p><strong>Makeup trend you love right now?</strong><br />\nI love the the ethnic look that’s coming back, there is a lot of passion in that trend and the tropical colours that are coming through – I particularly like the colour in between a pink and orange.</p>\n<img class=\"size-medium wp-image-2844 aligncenter\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06233815/JPG-Chosungah-brow-187x300.jpg\" alt=\"\" width=\"187\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06233815/JPG-Chosungah-brow-187x300.jpg 187w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06233815/JPG-Chosungah-brow.jpg 566w\" sizes=\"(max-width: 187px) 100vw, 187px\" />\n<p>&nbsp;</p>\n<p><a href=\"http://www.mecca.com.au/chosungah22/dong-gong-minn-brow-maker-black-brown/I-025693.html\">Dong Gong Minn Brow Maker, $24.</a></p>\n<p><strong>What’s your impression of Australian women and our beauty aesthetic?</strong><br />\nI’m finding that the Australian beauty reflects the healthy environment and lifestyle that you have. I’ve noticed women out for a run and exercising and it really reflects quality of life. Beauty isn’t just about putting on makeup but is about all aspects of your life. You are so lucky that you seem to enjoy lots of outdoor activity, good sunlight and I’m sure your SPF and moisturising is important. Australians match very well to our brand message of ‘mind beauty’.</p>\n<p><strong>Fav three products of all time?</strong><br />\nMy Chosungah 22 Bounce Up Pact Powder Foundation, C&amp;T Blender and the 16Brand Fingerpen.</p>\n<img class=\"size-medium wp-image-2842 aligncenter\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06233707/JPG-bounce--235x300.jpg\" alt=\"\" width=\"235\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06233707/JPG-bounce--235x300.jpg 235w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06233707/JPG-bounce-.jpg 722w\" sizes=\"(max-width: 235px) 100vw, 235px\" />\n<p><a href=\"http://www.mecca.com.au/chosungah22/bounce-up-pact-powder-foundation/V-025676.html?cgpath=brands-chosun#start=1\">Chosungah22 Bounce Up Pact Powder Foundation, $45.</a></p>\n<p><strong>Korean beauty product we need in our lives?</strong><br />\nOur new Donkey Milk Cream from 16Brand, because skin care is so important in your routine and this product incorporates the best of both worlds in terms of it being effective skincare but also giving you a beautiful finished look.</p>\n<p><strong>What is in your handbag right now?</strong><br />\nI have pulled it all apart at the moment but I always carry around our Dual Lip Tint Gloss, C&amp;T Blender and a 16Brand Fingerpen. Even if the world falls apart, these are the three products I always carry with me!</p>\n<img class=\"size-medium wp-image-2845 aligncenter\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06234340/JPG-CHo-blender-254x300.jpg\" alt=\"\" width=\"254\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06234340/JPG-CHo-blender-254x300.jpg 254w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06234340/JPG-CHo-blender.jpg 502w\" sizes=\"(max-width: 254px) 100vw, 254px\" />\n<p><a href=\"http://www.mecca.com.au/chosungah22/c-t-blender/V-025679.html\">Chosungah22 C&amp;T Blender, $56.</a></p>\n<p><strong>Night-time ritual?</strong><br />\nMoisturising is always key for me so I always use cotton pads that are completely drenched in my 16 Brand Multi-Toner that I keep in the freezer in a zip lock bag. Every night after my normal skincare routine, I take out one of the bags and place the cotton pads all around my face as a type of mask treatment for night-time. It’s very cooling and calming on your skin.</p>\n<p>&nbsp;</p>\n<img class=\"size-medium wp-image-2851 aligncenter\" src=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06235914/JPG-Toner-140x300.jpg\" alt=\"\" width=\"140\" height=\"300\" srcset=\"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06235914/JPG-Toner-140x300.jpg 140w, http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06235914/JPG-Toner.jpg 425w\" sizes=\"(max-width: 140px) 100vw, 140px\" />\n<p><a href=\"http://www.mecca.com.au/16-brand/multi-toner/I-027689.html?cgpath=brands-16bran#start=1\">16 Brand Multi Toner, $25.</a></p>\n","better_featured_image":{"source_url":"http://dcqiwphk5imn5.cloudfront.net/wp-content/uploads/2017/08/06233452/JPG-K-beauty.jpg"},"categories":[{"slug":"makeup","name":"Makeup"}]},"site":{"siteMetadata":{"title":"The Beauty Insider"}}},"pathContext":{"id":"0773e5d7-b1a4-5bf0-8c02-0d2ca6b98950"}}

